import MainSlider from "./modules/slider/slider-main";
import VideoPlayer from "./modules/playVideo";
import MiniSlider from "./modules/slider/slider-mini";
import Difference from "./modules/difference";
import Request from "./modules/request";
import Validation from "./modules/validation";
import Mask from "./modules/mask";
import Accordeon from "./modules/accordeon";
import Download from "./modules/download";
import axios from "axios";

window.addEventListener('DOMContentLoaded', () => {

    const download = new Download({
        triggers: '.module__info-book .download'
    });
    download.init()

    const accordeonModules = new Accordeon({
        triggers: '.moduleapp .plus',
    })
    accordeonModules.init();

    const requestHelp = new Request({
        form:'.join__evolution form', 
        adress:'assets/question.php'})
    requestHelp.init();

    const requestSchedule = new Request({
        form:'.schedule__form form', 
        adress:'assets/question.php'})
    requestSchedule.init();

    const validationEmail = new Validation('[name= "email"');
    validationEmail.init();

    const maskPhone = new Mask()
    maskPhone.init();

    const diffOld = new Difference({
        container: '.officerold',
        items: '.officer__card-item',
    })
    diffOld.init();

    const diffNew = new Difference({
        container: '.officernew',
        items: '.officer__card-item',
    })
    diffNew.init();

    const slider = new MainSlider({
        btns: '.next',
        container: '.page'
    });
    slider.init();

    const showUpSlider = new MiniSlider({
        container: '.showup__content-slider',
        next: '.showup__next',
        prev: '.showup__prev',
        activeClass: 'card-active',
    });
    showUpSlider.init();

    const modulesSlider = new MiniSlider({
        container: '.modules__content-slider',
        next: '.modules__info-btns .slick-next',
        prev: '.modules__info-btns .slick-prev',
        activeClass: 'card-active',
    });
    modulesSlider.init();

    const feedSlider = new MiniSlider({
        container: '.feed__slider',
        next: '.feed__slider .slick-next',
        prev: '.feed__slider .slick-prev',
        activeClass: 'feed__item-active',
        autoPlay: true
    });
    feedSlider.init();

    const moduleappSlider = new MainSlider({
        container: '.moduleapp',
        btns: '.sidecontrol a.next',
        next: '.moduleapp .nextmodule',
        prev: '.moduleapp .prevmodule',
    });
    moduleappSlider.init();
    
    const player = new VideoPlayer('.play', '.overlay');
    player.init()

})